@media (max-width: 1200px) {
    .container {
        width: 100%;
    }
    .works {
        &__wrapper {
            grid-template: repeat(3, 250px) / repeat(2, 450px);
        }
    }
    .references {
        &__item {
            &__picture {
                width: 330px;
                margin-left: 104px;
            }
            &__descr {
                width: 474px;
                font-size: 18px;
            }
        }
        &__arrow {
            &_left {
                margin: 157px 0 0 0;
                width: 5%;
            }
            &_right {
                margin-left: 933px;
                width: 5%;
            }
        }
    }
    .contact {
        &__name {
            width: 50%;
            margin-left: 265px;
        }
        &__email {
            width: 50%;
            margin-left: 265px;
        }
        &__message {
            width: 50%;
            margin-left: 265px;
        }
    }
}


