.works {   
    padding: 31px 217px 50px 223px; 
    background-color: #ebebeb;
    &__header {
        padding-top: 73px;
        font-family: Ubuntu;
        font-size: 22px;
        font-weight: 500;
        font-stretch: normal;
        font-style: normal;
        text-align: center;
        color: #787878;
    }
    &__wrapper {
        width: 100%;
        height: 100%;
        margin-top: 63px;
        margin-bottom: 150px;
        display: grid;
        grid-template: repeat(2, 250px) / repeat(3, 450px);
        justify-content: center;
        gap: 66px;

    }
    &__item {
        &:hover {           
            .works__item__descr {
                display: inline-block;
                position: absolute;
                z-index: 1;
                margin: 0px 0 0 -450px;
                font-family: Ubuntu;
                font-size: 20px;
                font-weight: bold;
                font-stretch: normal;
                font-style: normal;
                text-align: center;
                color: #ffffff;
                width: 450px;
                height: 283px;
                display: inline-block;
                opacity: 0.88;
                background-image: linear-gradient(to top, #2f3dfc, #641187), linear-gradient(to bottom, #616161, #616161);
                &:hover {
                    transition: all 0.5s;
                    filter: grayscale(60%);
                }
                p {
                    margin-top: 198px;
                    margin-bottom: -12rem;
                    margin-left: 272px;
                }
                
                
            }
         }
        &__picture {
            position: relative;
            width: 100%;
        }
        &__descr {
          display: none;
            
        }
    }

}