.top {
    background: url('../img/bg/bg_top.jpg') center center/cover no-repeat;
    &__header {
        display: inline-block;
        padding-top: 95px;
        font-family: UniNeue;
        font-size: 25px;
        font-weight: 900;
        font-stretch: normal;
        font-style: normal;
        text-align: left;
        color: #ffffff;
    }
    &__menu {
        margin-top: -43px;
        text-align: right;
        &__item {
            margin-right: 64px;
            font-family: Ubuntu;
            font-size: 15px;
            font-weight: bold;
            font-stretch: normal;
            font-style: normal;
            color: #ffffff;
            &:hover {
                text-decoration: none;
            }
        }
    }
    &__subheader {
        margin-top: 320px;
        font-family: Ubuntu;
        font-size: 35px;
        font-weight: bold;
        font-stretch: normal;
        font-style: normal;
        text-align: center;
        color: #ffffff;
    }
    &__consultancy {
        font-family: Ubuntu;
        font-size: 20px;
        font-weight: 300;
        font-stretch: normal;
        font-style: normal;
        text-align: center;
        color: #ffffff;
    }
    &__picture {
        width: 36px;
        height: 126px;
        display: inline-block;
        margin: 195px 0 0 539px;
    }
}

.floating{
    animation-name: floating;
    -webkit-animation-name: floating;
 
    animation-duration: 1.5s;   
    -webkit-animation-duration: 1.5s;
 
    animation-iteration-count: infinite;
    -webkit-animation-iteration-count: infinite;
}
 
@keyframes floating {
    0% {
        transform: translateY(0%);  
    }
    50% {
        transform: translateY(8%);  
    }   
    100% {
        transform: translateY(0%);
    }           
}
 
@-webkit-keyframes floating {
    0% {
        -webkit-transform: translateY(0%);  
    }
    50% {
        -webkit-transform: translateY(8%);  
    }   
    100% {
        -webkit-transform: translateY(0%);
    }           
}