.contact {
    background-color: black;
    &__header {
        padding-top: 150px;
        font-family: Ubuntu;
        font-size: 30px;
        font-weight: bold;
        font-stretch: normal;
        font-style: normal;
        text-align: center;
        color: #ffffff;
    }
    &__name {
        width: 480px;
        height: 60px;
        padding-left: 14px;
        color: white;
        background-color: black;
        border: none;
        border-bottom: 1px solid white;
    }
    &__email {
        width: 480px;
        height: 60px;
        margin-left: 169px;
        padding-left: 14px;
        color: white;
        background-color: black;
        border: none;
        border-bottom: 1px solid white;
    }
    &__message {
        display: block;
        width: 1136px;
        height: 60px;
        margin-top: 30px;
        padding-left: 14px;
        color: white;
        background-color: black;
        border: none;
        border-bottom: 1px solid white;
    }
    &__btn {
        width: 235px;
        height: 90px;
        margin-top: 73px;
        margin-bottom: 93px;
        background-color: rgb(37, 37, 33);
        border: none;
        position: relative;
        left: 41%;
        font-family: UniNeue;
        font-size: 30px;
        font-weight: bold;
        font-stretch: normal;
        font-style: normal;
        text-align: center;
        color: #ffffff;
        cursor: pointer;
    }
}