.overlay {
    display: none;
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    background-color: rgba(0,0,0, .56);    
}

// Modal (Contact)

.modal {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 380px;
    height: 60px;
    background-color: white;
    &__subtitle {
        margin-top: 12px;
        font-family: Ubuntu;
        font-size: 20px;
        font-weight: 300;
        font-stretch: normal;
        font-style: normal;
        text-align: center;
        color: #5f5f5f;
    }
    &__close {
        position: absolute;
        top: -14px;
        right: 5px;
        font-size: 36px;
        cursor: pointer;
    }
}

// Modal (Services)

.modal {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 380px;
    height: 60px;
    background-color: white;
    &__services {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 380px;
        height: 420px;
        background-color: white;
    }
    &__subtitle {
        margin-top: 12px;
        font-family: Ubuntu;
        font-size: 20px;
        font-weight: 300;
        font-stretch: normal;
        font-style: normal;
        text-align: center;
        color: #5f5f5f;
    }
    &__close {
        position: absolute;
        top: -14px;
        right: 5px;
        font-size: 36px;
        cursor: pointer;
    }
    &__name {
        width: 66%;
        height: 12%;
        display: inline-block;
        margin: 25px 0 0 54px;
        border: none;
        border-bottom: 1px solid rgb(104, 95, 95);
        
    }
    &__email {
        width: 66%;
        height: 12%;
        display: inline-block;
        margin: 25px 0 0 54px;
        border: none;
        border-bottom: 1px solid rgb(104, 95, 95);
    }
    &__message {
        width: 66%;
        height: 12%;
        display: inline-block;
        margin: 25px 0 0 54px;
        border: none;
        border-bottom: 1px solid rgb(104, 95, 95);
    }
    &__btn {
        width: 160px;
        height: 59px;
        margin-top: 73px;
        margin-bottom: 93px;
        background-color: #252521;
        border: none;
        position: relative;
        left: 28%;
        font-family: UniNeue;
        font-size: 30px;
        font-weight: 700;
        font-stretch: normal;
        font-style: normal;
        text-align: center;
        color: #fff;
    }
}