.references {
    &__header {
        padding-top: 73px;
        font-family: Ubuntu;
        font-size: 22px;
        font-weight: 500;
        font-stretch: normal;
        font-style: normal;
        text-align: center;
        color: #787878;
    }
    &__wrapper {
        display: flex;
        flex-wrap: wrap;
        position: relative;
    }
    &__item {
        &__picture {
            width: 450px;
            margin-top: 73px;
            float: left;
            
        }
        &__name {
            margin: 70px 7px 27px 81px;
            display: inline-block;
            font-family: Ubuntu;
            font-size: 44.5px;
            font-weight: bold;
            font-stretch: normal;
            font-style: normal;
            color: #303030;
        }
        &__descr {
            width: 650px;
            margin: 10px 0 0 486px;
            font-family: Ubuntu;
            font-size: 20px;
            font-weight: 300;
            font-stretch: normal;
            font-style: normal;
            color: #5f5f5f;
        }
    }
    &__arrow {
        &_left {
            position: absolute;
            margin: 157px 0px 0px -112px;
            width: 7%;
            cursor: pointer;
            &:hover {
                transition: 0.5s all;
                background-color:rgba(0,0,0,0.5);
            }
        }
        &_right {
            position: absolute;
            margin: 157px 0px 0px 0px;
            width: 7%;
            margin-left: 1130px;
            cursor: pointer; 
            &:hover {
                transition: 0.5s all;
                background-color:rgba(0,0,0,0.5);
            }
        }
    }
    &__logos {
        margin-top: 146px;
        margin-bottom: 48px;
    }
    &__logo {
        padding-right: 20px;
    }
}

.fade-in {
    animation: fadeIn ease 1s;
    -webkit-animation: fadeIn ease 1s;
    -moz-animation: fadeIn ease 1s;
    -o-animation: fadeIn ease 1s;
    -ms-animation: fadeIn ease 1s;
}
  
@keyframes fadeIn {
0% {opacity:0;}
100% {opacity:1;}
}

@-moz-keyframes fadeIn {
0% {opacity:0;}
100% {opacity:1;}
}

@-webkit-keyframes fadeIn {
0% {opacity:0;}
100% {opacity:1;}
}

@-o-keyframes fadeIn {
0% {opacity:0;}
100% {opacity:1;}
}

@-ms-keyframes fadeIn {
0% {opacity:0;}
100% {opacity:1;}
}