.middle {
    &__header {
        margin-top: 157px;
        font-family: Ubuntu;
        font-size: 59px;
        font-weight: bold;
        font-stretch: normal;
        font-style: normal;
        text-align: center;
        color: #303030;
    }
    &__subheader {
        font-family: Ubuntu;
        font-size: 22px;
        font-weight: 500;
        font-stretch: normal;
        font-style: normal;
        text-align: center;
        color: #787878;
    }
    &__wrapper {
        display: flex;
        gap: 100px;
    }
    &__item {
        &__picture {
            display: block;
            margin: 106px auto;
        }
        &__header {
            margin-top: 69px;
            margin-bottom: 74px;
            font-family: Ubuntu;
            font-size: 40px;
            font-weight: bold;
            font-stretch: normal;
            font-style: normal;
            line-height: normal;
            text-align: center;
            color: #787878;
        }
        &__descr {
            font-family: UniNeue;
            font-size: 18px;
            font-weight: normal;
            font-stretch: normal;
            font-style: normal;
            line-height: normal;
            text-align: left;
            color: #000000;
        }
    }
    &__btn {
        width: 295px;
        height: 63px;
        margin: 148px auto;
        position: relative;
        left: 50%;
        transform: translate(-50%, 0); 
        border-radius: 23px;
        background-image: linear-gradient(to top, #2f3dfc, #641187), linear-gradient(to bottom, #616161, #616161);
        font-family: UniNeue;
        font-size: 20px;
        font-weight: 900;
        font-stretch: normal;
        font-style: normal;
        color: #ffffff;
    }
}