.footer {
    width: 100%;
    height: 203px;
    background-image: linear-gradient(to top, #2f3dfc, #641187), linear-gradient(to bottom, #343434, #343434);
    &__header {
        display: inline-block;
        position: relative;
        padding-top: 95px;
        font-family: UniNeue;
        font-size: 25px;
        font-weight: 900;
        font-stretch: normal;
        font-style: normal;
        text-align: left;
        color: #ffffff;
    }
    &__address {
        width: 9%;
        display: inline-block;
        position: absolute;
        margin: 100px 0 0 210px;
        font-family: Ubuntu;
        font-size: 16.5px;
        font-weight: 500;
        font-stretch: normal;
        font-style: normal;
        line-height: normal;
        letter-spacing: -0.66px;
        text-align: left;
        color: #ffffff;
    }
    &__email {
        margin-left: 626px;
        margin-top: -55px;
        &__picture {
            float: left;
        }
        &__descr {
            display: inline-block;
            font-family: Ubuntu;
            font-size: 16.5px;
            font-weight: bold;
            font-stretch: normal;
            font-style: normal;
            color: #ffffff;
        }
    }
    &__phone {
        margin-left: 626px;
        
        &__picture {
            float: left;
        }
        &__descr {
            display: inline-block;
            font-family: Ubuntu;
            font-size: 16.5px;
            font-weight: bold;
            font-stretch: normal;
            font-style: normal;
            color: #ffffff;
        }
    }
    &__icons {
        margin-top: -40px;
        margin-right: 35px;
        float: right;
        a {
            padding-right: 5px;
        }
        &__picture {
            padding-right: 5px;
        }
    }
    &__rights {
        margin-right: 35px;
        font-family: Ubuntu;
        font-size: 12px;
        font-weight: 300;
        font-stretch: normal;
        font-style: normal;
        color: #ffffff;
        float: right;
    }
}